.App {
  text-align: center;
}


.App-header {
  background-color: #f8fcf4;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #202020;
}

.App-header .container { 
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  height: 100vh;
  max-width: 100vw;
  scroll-behavior: smooth;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.App-header .container::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.App-header .container {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.App h3 {
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 1vh;
}

.App h6 { 
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0.2vh;
  font-weight: normal;
}

.App-header .child{ 
  scroll-snap-align: start;
  margin-bottom: 100vh;
}

.App-link {
  color: #61dafb;
} 

.logo3d { 
  box-shadow: inset 0px -100px 41px -74px rgba(0,0,0,0.1);
  border-radius: 50vh;
  transition: 0.2s;
}

.logo3d:hover { 
  box-shadow: inset 0px -100px 41px -74px rgba(0,0,0,0.1);
  border-radius: 50vh;
}



